<template>
  <div :class="className">
    <div class="chooseFileButton">
      <el-button
        :disabled="editFlagOnly"
        class="buttonText"
        size="small"
        type="primary"
      >
        点击上传
      </el-button>
      <input
        id="file"
        ref="fileGet"
        :disabled="editFlagOnly"
        type="file"
        value=""
        @change="fileUpLoad"
      >
    </div>
    <div
      v-if="isNotUpload && fileList.length"
      class="successObj"
    >
      <p class="objText">
        {{ fileList[0].name[10] === '/' ? fileList[0].name.slice(11) : fileList[0].name }}
      </p>
    </div>

    <div
      v-if="success"
      class="successObj"
    >
      <p
        v-if="back_file_name"
        class="objText"
      >
        {{ back_file_name[10] === '/' ? back_file_name.slice(11) : back_file_name }}
      </p>
      <P
        class="closeImg"
        @click="cutFile"
      >
        <img
          src="data:image/png;base64,">
      </P>
    </div>

    <div
      v-if="!success && (showStatus || showAgainButton)"
      class="hidingPath"
    >
      <div
        v-if="showStatus"
        class="progressBar"
      >
        <span v-if="status ===0">切片进度:{{ percent+'%' }} </span>
        <span v-else>上传进度:{{ percent+'%' }} </span>
        <progress
          ref="progress"
          :value="percent"
          :data-file_file_md5="percent"
          max="100/"
        />
      </div>
      <div
        v-if="showAgainButton"
        ref="submit"
        class="submitButton"
        @click="submitFile"
      >
        重新上传
      </div>
    </div>
  </div>
</template>
<script>
import SparkMD5 from 'spark-md5'
import axios from 'axios'
import * as URL from '~/lib/api'

export default {
  props: ['fileList', 'className', 'validFileName', 'inputType', 'editFlagOnly'],
  data () {
    return {
      uploadFileLine: null,
      isNotUpload: true,
      status: 0, // 进度条的状态，0表示正在切片，1表示正在上传
      percent: 0, // 进度条百分比
      file_name: '',
      chunkSize: 10485760, // 单词上传的图片大小,10M
      // chunkSize:2097152,//单词上传的图片大小,2M
      chunksCount: 0, // 这个文件要上传的次数
      pices: [],
      picesMD5: [],
      shard_index_list: [],
      file_md5: '', // 上传问价你的md5值
      dealCount: 0, // 上传次数
      success: false,
      showStatus: false, // 是否展示进度
      showAgainButton: false, // 是否展示重新提交按钮,
      back_file_name: '' // 上传成功后返回的文件名
    }
  },
  watch: {},
  mounted () {
  },
  methods: {
    clearFiles () {
      this.$refs.fileGet.value = ''
      this.isNotUpload = true
      this.back_file_name = ''
      this.zeroFile()
    },
    zeroFile () {
      // 归零,数据重置
      this.status = 0
      this.percent = 0
      this.file_name = ''
      this.chunksCount = 0
      this.pices = []
      this.picesMD5 = []
      this.shard_index_list = []
      this.file_md5 = ''
      this.dealCount = ''
      this.success = !!this.back_file_name
      this.showStatus = false
      this.showAgainButton = false
      this.back_file_name = this.back_file_name ? this.back_file_name : ''
    },
    fileUpLoad: function (e) {
      this.zeroFile()
      console.log(e.target.files[0])
      if (!e.target.files[0]) return
      let file = e.target.files[0]
      this.file_name = file.name || ''
      if (this.inputType === 13 && typeof this.validFileName === 'function' && !this.validFileName(this.file_name)) {
        return this.$message({
          message: '文件名,格式不对！',
          type: 'warning'
        })
      }
      this.isNotUpload = false
      this.showStatus = true
      let fileSize = file.size
      let currentChunk = 0
      let that = this
      // 文件分割方法（注意兼容性）
      let blobSlice = File.prototype.mozSlice || File.prototype.webkitSlice || File.prototype.slice
      var chunksCount = Math.ceil(fileSize / this.chunkSize)
      this.chunksCount = chunksCount

      // 创建md5对象（基于SparkMD5）
      let spark = new SparkMD5()
      let fileReader = new FileReader()
      // 每块文件读取完毕之后的处理
      fileReader.onload = function (e) {
        // console.log("event",e)
        console.log('读取文件', currentChunk + 1, '/', chunksCount)
        // 每块交由sparkMD5进行计算
        spark.appendBinary(e.target.result) // 总文件MD5的计算

        var spark1 = new SparkMD5() // 单片文件md5的计算
        spark1.appendBinary(e.target.result)
        that.picesMD5.push(spark1.end())

        currentChunk++
        var percent = (currentChunk / chunksCount * 100).toFixed(2)
        that.percent = percent

        // 如果文件处理完成计算MD5，如果还有分片继续处理
        if (currentChunk < chunksCount) {
          loadNext(currentChunk, that.chunkSize)
        } else {
          var md5string = spark.end()
          that.file_md5 = md5string
          // console.info("计算的Hash", md5string);
          // console.log(9999999999,that.picesMD5)
          // eslint-disable-next-line no-inner-declarations
          function uploadFileLine () {
            that.dealCount++
            var successPackage = 0 // 成功上传的包的计数
            if (that.dealCount <= 3 && !that.success) {
              that
                .getUnuploadShardList({
                  total_size: fileSize,
                  shard_size: that.chunkSize,
                  file_md5: md5string
                })
                .then(
                  function (data) {
                    if (data.data.code === 0) {
                      let result = data.data.result
                      that.shard_index_list = result.shard_index_list

                      if (result.shard_index_list && result.shard_index_list.length === 0) {
                        // 说明这个文件已经上传过，

                        return that
                          .uploadIsSuccess({
                            file_md5: that.file_md5
                          })
                          .then(function (result) {
                            if (result.data.code === 0) {
                              that.$message('文件已经上传成功!')
                              that.success = true
                              that.back_file_name = result.data.result.object
                              that.status = 1
                              that.percent = 100
                              that.$emit('uploadSuccess', {
                                download_file_md5: result.data.result.file_md5,
                                download_url_object: that.back_file_name,
                                size: result.data.result.size
                              })
                            } else {
                              uploadFileLine()
                            }
                          })
                          .catch(function (err) {
                            console.log('失败', err)
                            uploadFileLine()
                          })
                      } else {
                        // console.log("else",22222222222222222222)
                        return result.shard_index_list
                      }
                    }
                  },
                  function (err) {
                    console.log('分片上传失败', err)
                    that.$message('任务初始化失败，请重试')
                    that.showAgainButton = true
                    that.status = 0
                    that.percent = 0
                  }
                )
                .then(function (shard_index_list) {
                  // console.log(111111110,shard_index_list)
                  that.status = 1
                  that.percent = 0
                  that.picesFileUpload(shard_index_list, successPackage, that) // 上传分片后的文件
                })
            } else if (that.success) {
              // console.log("已经上传成功")
              that.$message('文件上传成功!')
              that.$emit('uploadSuccess', {
                download_file_md5: that.file_md5,
                download_url_object: that.back_file_name,
                size: this.result.data.result.size
              })
            } else {
              // console.log("三次上传后仍然出错，需求重新上传")
              that.$message('文件上传出错，请重试')
              that.showAgainButton = true
              that.status = 0
              that.percent = 0
            }
          }
          this.uploadFileLine = uploadFileLine
          uploadFileLine()
        }
      }

      // 处理单片文件的上传
      function loadNext (currentChunk, chunkSize) {
        var start = currentChunk * chunkSize
        var end = start + chunkSize >= file.size ? file.size : start + chunkSize
        that.pices.push(blobSlice.call(file, start, end))

        fileReader.readAsBinaryString(blobSlice.call(file, start, end))
      }

      loadNext(currentChunk, that.chunkSize)
    },
    getUnuploadShardList: function (params) {
      // 分片任务初始化接口
      return axios.post(URL.getUnuploadShardList, params)
    },
    uploadShard: function (query, blob) {
      // var xhr = new XMLHttpRequest()
      // xhr.open('POST', URL.uploadShard + '?file_md5=' + query.file_md5 +"&shard_index="+ query.shard_index+'&shard_md5='+query.shard_md5, true);
      // xhr.send(blob);
      return axios.post(
        URL.uploadShard +
          '?file_md5=' +
          query.file_md5 +
          '&shard_index=' +
          query.shard_index +
          '&shard_md5=' +
          query.shard_md5 +
          '&file_name=' +
          query.file_name +
          '&noToken=true',
        blob
      )
    },
    uploadIsSuccess: function (params) {
      return axios.post(URL.uploadIsSuccess, params, { async: true }) // 同步上传
    },
    picesFileUpload: function (shard_index_list, successPackage, that) {
      if (shard_index_list && shard_index_list.length !== 0) {
        var promiseAll = []
        shard_index_list.forEach((item, index) => {
          var promise = new Promise((resolve, reject) => {
            // console.log("that.picesMD5[item]",that.picesMD5[item])
            that
              .uploadShard(
                {
                  file_md5: that.file_md5,
                  shard_index: item,
                  shard_md5: that.picesMD5[item],
                  file_name: that.file_name
                },
                that.pices[item]
              )
              .then(function (data) {
                if (data.data.code === 0) {
                  successPackage++
                  that.status = 1
                  that.percent = (successPackage / that.shard_index_list.length * 100).toFixed(2) // 上传成功百分比
                  resolve(data)
                }
              })
              .catch(function (err) {
                reject(err)
              })
          })
          promiseAll.push(promise)
        })
        Promise.all(promiseAll)
          .then(function (resultAllData) {
            if (resultAllData) {
              if (successPackage === that.shard_index_list.length) {
                console.log('所有待上传的包都上传成功了')
                that
                  .uploadIsSuccess({
                    file_md5: that.file_md5
                  })
                  .then(function (result) {
                    if (result.data.code === 0) {
                      that.success = true
                      that.back_file_name = result.data.result.object
                      that.success = true
                      that.$emit('uploadSuccess', {
                        download_file_md5: result.data.result.file_md5,
                        download_url_object: that.back_file_name,
                        size: result.data.result.size
                      })
                      that.$message('全部分片已经上传成功了')
                    }
                  })
                  .catch(function (err) {
                    that.$message(err)
                    // that.$message("任务初始化失败，请重试");
                    that.showAgainButton = true
                    that.status = 0
                    that.percent = 0
                  })
              } else {
                // console.log("重新上传一次uploadFileLine()")
                this.uploadFileLine()
              }
            }
          })
          .catch(function (err) {
            console.log('err', err)
            that.zeroFile()
            that.showAgainButton = true
            that.status = 0
            that.percent = 0
          })
      } else {

      }
    },
    submitFile: function () {
      this.$refs.fileGet.value = ''
      this.$refs.fileGet.click(this.$refs.fileGet, this.$refs.fileGet.$event)
    },
    cutFile: function () {
      this.back_file_name = ''
      this.zeroFile()
      this.$refs.fileGet.value = ''
      this.$emit('uploadSuccess', { download_file_md5: '', download_url_object: '', size: 0 })
    }
  }
}
</script>
<style lang="less">
  .submitButton {
    width: 80px;
    height: 40px;
    line-height: 40px;
    border-radius: 10px;
    background-color: #0c57a1;
    color: #fff;
    text-align: center;
    margin-top: 10px;
    font-size: 13px;
  }

  .hidingPath {
    width: 80%;
    height: auto;
    padding: 10px 20px 20px 20px;
    border: 1px solid #cccc;
    border-radius: 0 0 10px 10px;
    background-color: #f4f4f8;
  }

  .chooseFileButton {
    height: 40px !important;
    width: 90px;
    position: relative;
  }

  #file {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    opacity: 0;
  }

  .successObj {
    width: 100%;
    height: 20px;
    line-height: 20px;
    background-color: #f4f4f8;
    box-sizing: border-box;
    padding: 0px 10px;
    position: relative;

    .objText {
      font-size: 14px;
      color: #606266;
    }

    .closeImg {
      width: 12px;
      height: 12px;
      position: absolute;
      top: 4px;
      right: 5px;

      img {
        display: block;
        width: 12px;
        height: 12px;
      }
    }
  }

  .newButtonStyle .chooseFileButton span {
    line-height: 20px !important;
  }

  .disabled {
    cursor: not-allowed;
  }

</style>
